import { connect } from "react-redux";
import './css/DataPanel.css';
import {useState, useEffect, useMemo, useRef } from 'react';
import { useFilters, useTable } from "react-table";
import { csvFromString } from "atlas-vis";
import { addToStack } from "./Util";


function DataPanel (props) {

    const [activeTbl, setActiveTbl] = useState(null);
    const [dataScopeInfo, setDataScopeInfo] = useState(null);
    const inputFile = useRef(null);

    const cols = useMemo(() => {
        if (activeTbl === null) return [{Header: " ", accessor: " "}];
        else {
            let fields = activeTbl.fields;
            let result = [];
            for (let f of fields) {
                //if (f === "atlas_rowId") continue;
                result.push({
                    Header: f === "atlas_rowId" ? "Row ID" : f,
                    accessor: f,
                    filter: "equals",
                    Cell: ({value}) => activeTbl.getRawValue(f, value)
                })
            }
            return result;
        }
    }, [activeTbl])

    const data = useMemo(() => {
        if (activeTbl === null) return [{col: 1}];
        else {
            return activeTbl.data;
        }
    }, [activeTbl])

    const initState = { hiddenColumns: ['atlas_rowId'] };

    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,
        rows,
        prepareRow,
        setFilter,
        setAllFilters,
    } = useTable({columns: cols, data: data, initialState: initState}, useFilters);

    const importData = function() {
        inputFile.current.click();
    }

    const fileChanged = function(e) {
        e.stopPropagation();
        e.preventDefault();
        let f = e.target.files[0];
        const reader = new FileReader();
        reader.onload = function() {
            addToStack(props.scene, props.datasets, props.undoStack);
            let tbl = csvFromString(reader.result, f.name);
            let ds = props.datasets.slice();
            ds.push(tbl);
            props.loadDataset(ds);
            setActiveTbl(tbl);
            // console.log("data imported", props.selected);
        }
        if (f)
            reader.readAsText(f);
    }

    const getHeaderIcon = function(field) {
        if (!activeTbl)
            return <span></span>;
        switch (activeTbl.getFieldType(field)) {
            case "string":
                return <img src="/app/icons/string.png" alt="(string) " width="20px" height="10px" style={{"margin":"0 4px 2px 0"}}></img>;
            case "date":
                return <img src="/app/icons/date.png" alt="(date) " width="15px" style={{"margin":"0 3px 2px 0"}}></img>;
            default:
                return <img src="/app/icons/number.png" alt="(number) " width="12px" height="12px" style={{"margin":"0 2px 3px 0"}}></img>;
        }
    }

    useEffect(() => {
        if (!props.datasets || props.datasets.length === 0) {
            setActiveTbl(null);
            setAllFilters([]);
            setDataScopeInfo(null);
            return;
        }
        if (props.selected.length === 0 && !props.directSelected) {
            setActiveTbl(props.datasets[0]);
            setAllFilters([]);
            setDataScopeInfo(null);
        } else if (props.selected.length > 0) {
            const item = props.scene.getItem(props.selected[0]);
            if (!item) {
                setActiveTbl(props.datasets[0]);
                setAllFilters([]);
                setDataScopeInfo(null);
                return;
            }
            if (item.dataScope) {
                setActiveTbl(item.dataScope.dataTable);
                const filters = item.dataScope.filters;
                for (let f in filters) {
                    setFilter(f, filters[f]);
                }
                setDataScopeInfo("Selected " + item.type + " has " + item.dataScope.numTuples +  " attached data rows");
            } else {
                setActiveTbl(null);
                setAllFilters([]);
                setDataScopeInfo("Selected " + item.type + " has no attached data");
            } 
        } else if (props.directSelected) {
            const itm = props.scene.getItem(props.directSelected);
            if (!itm) {
                setActiveTbl(props.datasets[0]);
                setAllFilters([]);
                setDataScopeInfo(null);
            }
            if (itm.dataScope) {
                setActiveTbl(itm.dataScope.dataTable);
                const filters = itm.dataScope.filters;
                for (let f in filters) {
                    setFilter(f, filters[f]);
                }
                setDataScopeInfo("Selected " + itm.type + " has " + itm.dataScope.numTuples +  " attached data rows");
            } else {
                setActiveTbl(null);
                setAllFilters([]);
                setDataScopeInfo("Selected " + itm.type + " has no attached data");
            } 

        }
    }, [props.selected, props.directSelected, props.scene, props.datasets, setAllFilters, setFilter]);

    return (
        <div id="dataPanel">
            <div id="datasetInfo">
                {props.datasets.map(d => 
                <div className={activeTbl && activeTbl.id === d.id ? "activeTableTab" : "tableTab"} key={d.id} onClick={(e) => setActiveTbl(d)}>
                    {d.name + " (" + d.getRowCount() + " rows)"}
                </div>)}
                <button id="csvBtn" onClick={importData}>Import Data</button>
                <input type='file' id='file' ref={inputFile} style={{display: 'none'}} accept=".csv" onChange={fileChanged}/>
            </div>
            <div id="dataScopeInfo" style={{display: dataScopeInfo ? "block" : "none"}}>
                {dataScopeInfo ? dataScopeInfo : ""}
            </div>
            <div id="tableDiv">
                <table id="dataTable" {...getTableProps()}>
                        <thead>
                            {// Loop over the header rows
                                headerGroups.map(headerGroup => (
                                    // Apply the header row props
                                    <tr {...headerGroup.getHeaderGroupProps()}>
                                        {// Loop over the headers in each row
                                            headerGroup.headers.map(column => (
                                                // Apply the header cell props
                                                <th {...column.getHeaderProps()}>
                                                    {getHeaderIcon(column.Header)}
                                                    {// Render the header
                                                        column.render('Header')
                                                    }
                                                </th>
                                            ))}
                                    </tr>
                                ))}
                        </thead>
                        {/* Apply the table body props */}
                        <tbody {...getTableBodyProps()}>
                            {// Loop over the table rows
                                rows.map(row => {
                                    // Prepare the row for display
                                    prepareRow(row)
                                    return (
                                        // Apply the row props
                                        <tr {...row.getRowProps()}>
                                            {// Loop over the rows cells
                                                row.cells.map(cell => {
                                                    // Apply the cell props
                                                    return (
                                                        <td {...cell.getCellProps()}>
                                                            {// Render the cell contents
                                                                cell.render('Cell')}
                                                        </td>
                                                    )
                                                })}
                                        </tr>
                                    )
                                })}
                        </tbody>
                    </table> 
            </div>
            
        </div>
    );
}

const mapStateToProps = state => ({
    selected: state.canvas.selected,
    directSelected: state.canvas.directSelected
});

export default connect(mapStateToProps)(DataPanel);