import React from "react";
import './css/Canvas.css';
import { connect } from "react-redux";
import { useEffect } from 'react';
import { deleteItems, select, updateItem } from "./canvasSlice";
import store from "./store";
import * as PolylineTool from "./tools/PolylineTool";


const mapStateToProps = state => ({
    selected: state.canvas.selected,
    itemUpdated: state.canvas.itemUpdated,
    scaleUpdated: state.canvas.scaleUpdated,
    dataItemJoined: state.canvas.dataItemJoined,
    itemsDeleted: state.canvas.itemsDeleted
});

function Canvas(props) {

    useEffect(() => {
        props.renderer.render(props.scene);
    }, [props.scene, props.renderer, props.itemUpdated, props.scaleUpdated, props.dataItemJoined, props.itemsDeleted])

    useEffect(() => {

        const translateByKey = (x, y) => {
            let s = store.getState().canvas.selected;
            if (s && s.length > 0) {
                let item = props.scene.getItem(s[0]);
                if (item) {
                    props.scene.translate(item, x, y);
                    store.dispatch(updateItem({id: item.id, type: item.type, properties: {x: item.x, y: item.y}}));
                }
            }
            let ds = store.getState().canvas.directSelected;
            if (ds) {
                let itm = props.scene.getItem(ds);
                if (itm.parent.type === "path") {
                    itm._doTranslate(x, y);
                    store.dispatch(updateItem({id: itm.parent.id, type: itm.parent.type, properties: {x: itm.parent.x, y: itm.parent.y}}));
                }
            }
        }

        document.addEventListener('keydown', (e) => {
            // e.preventDefault();
            // e.stopPropagation();
            switch(e.key) {
                case "Delete": {
                    let s = store.getState().canvas.selected;
                    if (s && s.length > 0) {
                        for (let i = s.length - 1; i >= 0; i--) {
                            let item = props.scene.getItem(s[i]);
                            if (item && item.parent === props.scene) {
                                store.dispatch(select([]));
                                props.scene.removeItem(item);
                                store.dispatch(deleteItems([item].map(d => d.id)));
                            }
                        }
                    }
                    break;
                }
                case "ArrowLeft": {
                    translateByKey(-5, 0);
                    break;
                }
                case "ArrowRight": {
                    translateByKey(5, 0);
                    break;
                }
                case "ArrowUp": {
                    translateByKey(0, -5);
                    break;
                }
                case "ArrowDown": {
                    translateByKey(0, 5);
                    break;
                }
                case "Escape": {
                    if (store.getState().tool.activeTool === "Pen") {
                        PolylineTool.finish(props.scene, props.datasets, props.renderer, props.undoStack);
                    }
                    break;
                }
                default:
                    break;
            }
        });
    },[props.scene, props.datasets, props.renderer, props.undoStack])

    switch(props.type) {
        case "canvas":
            return (
                <canvas className="canvas" id="canvas"></canvas>
            );
        default:
        case "svg":
            return (
                <svg className="canvas" id="canvas"></svg>
            );
    }
}

export default connect(mapStateToProps)(Canvas);;