import Canvas from './Canvas';
import { useEffect, useState } from 'react';
import * as atlas from "atlas-vis";
import EventLayer from './EventLayer';
import ToolBar from './ToolBar';
import Inspector from './Inspector';
import LayerView from './LayerView';
import DataPanel from './DataPanel';
import './css/App.css';
import store from "./store";
import { directSelect, select, toggleAxisHandle } from "./canvasSlice";
import InteractiveLegend from './InteractiveLegend';

function Authoring(props) {

  const [renderer] = useState(atlas.renderer("svg", "canvas"));
  const [scene, setScene] = useState(atlas.scene());
  const [spec, setSpec] = useState("");
  const [datasets, setDatasets] = useState([]);
  const [undoStack, setUndoStack] = useState([]); 
  const [redoStack, setRedoStack] = useState([]); 

  useEffect(() => {
    document.body.style.backgroundColor = "#ddd";
    document.body.style.overflow = "hidden";
  }, [])

  useEffect(() => {
    if (spec) {
      fetch("app/scenes/" + spec + ".alscn")
      .then(response => response.json())
      .then(result => {
        loadScene(result);
      });
    }
  }, [spec]);

  useEffect(() => {
    store.dispatch(toggleAxisHandle({visible: false}));
    store.dispatch(select([]));
    store.dispatch(directSelect(null));
    renderer.clear();
    renderer.render(scene);
  }, [renderer, scene]);

  const loadScene = (json) => {
    let loader = atlas.sceneLoader();
    let scn = loader.load(json);
    setScene(scn);
    setDatasets(Object.values(loader.tables));
    //setDatasets(Object.values(scn.getDataTables()));
  }

  const demos = [
    "Rect",
    "BarChartHorz",
    "BarChartVert",
    "BoxPlot",
    "BulletChart",
    "DivergingBarChart",
    "GanttChart",
    "GroupedBarChart",
    "HeatMap",
    "Histogram",
    "MosaicPlot",
    "StackedBarChart",
    "TowerChart",
    "Treemap",
    "WaffleChart",
    "WaterfallChart",
    "BubblePlot",
    "CirclePacking",
    "DotPlot",
    "DoughnutChart",
    "PieChart",
    "RadialBarChart",
    "Scatterplot",
    "Sunburst",
    "BumpChart",
    "ConnectedScatterPlot",
    "DumbbellChart",
    "LineGraph",
    "LineGraph2",
    "LollipopChart",
    "MultiLineGraph",
    "ParallelCoordinates",
    "RangeChart",
    "SlopeGraph",
    "StringlineChart",
    "AreaChart",
    "DensityPlot",
    "RadarChart",
    "RidgelinePlot",
    "RoseChart",
    "StackedAreaChart",
    "StellarChart",
    "StreamGraph",
    "ViolinPlot",
    "Isotype",
    "GridAreaChart",
    "MultipleBarCharts",
    "MultipleBoxPlots",
    "MultiplePieCharts",
    "ScatterplotMatrix",
    "Sparklines",
    "GridStackedAreaChart",
    "MultipleViolinPlots"
  ]

  // const loadDataset = (d) => {
  //   setDatasets(datasets.concat([d]));
  // }

  const resetStacks = () => {
    setUndoStack([]);
    setRedoStack([]);
  }

  return (
    <div style={{overflow: "hidden"}}>
      <Canvas scene={scene} renderer={renderer} datasets={datasets} undoStack={undoStack} redoStack={redoStack} type="svg"></Canvas>
      <EventLayer scene={scene} renderer={renderer} datasets={datasets} undoStack={undoStack} redoStack={redoStack}></EventLayer>
      <div id="specList" style={{display: 'none'}}>
        <ul>
          {demos.map(d => <li key={d} onClick={(e) => setSpec(d)}>{d}</li>)}
        </ul>
      </div>
      <DataPanel scene={scene} renderer={renderer} datasets={datasets} undoStack={undoStack} loadDataset={setDatasets}></DataPanel>
      <LayerView scene={scene} renderer={renderer}></LayerView>
      <ToolBar scene={scene} renderer={renderer} datasets={datasets} undoStack={undoStack} redoStack={redoStack} resetStacks={resetStacks} loadScene={loadScene}></ToolBar>
      <Inspector scene={scene} renderer={renderer} datasets={datasets} undoStack={undoStack}></Inspector>
      <InteractiveLegend scene={scene} renderer={renderer}></InteractiveLegend>
      <input type="text" id="textElement" style={{"visibility": 'hidden', "position": "absolute"}}></input>
    </div>
  )
}

export default Authoring;
