import { properties } from "./Defs";
import * as atlas from "atlas-vis";
import store from "./store";
import { updateRedoStack, updateUndoStack } from "./canvasSlice";

const addToStack = (scene, datasets, stack, type) => {
    let stackType = type ? type : "undo";
    let scn = scene.toJSON();
    for (let t of datasets) {
        if (!(t.id in scn.tables)) {
            scn.tables[t.id] = t.toJSON();
        }
    }
    // let s = stack.slice();
    // s.push(JSON.stringify(scn));
    // setStack(s);
    stack.push(JSON.stringify(scn));
    // console.log("added to stack", stackType, stack.length);
    if (stackType === "undo")
        store.dispatch(updateUndoStack({"length": stack.length}));
    else if (stackType === "redo")
        store.dispatch(updateRedoStack({"length": stack.length}));
}

const getSVGPathData = (items, vxSize) => {
    let d = "";
    for (let i of items) {
        if (i.getSVGPathData)
            d += i.getSVGPathData();
        else if (i.bounds) {
            let b = i.bounds;
            d += ["M", b.left, b.top].join(" ");
            d += ["L", b.right, b.top].join(" ");
            d += ["L", b.right, b.bottom].join(" ");
            d += ["L", b.left, b.bottom, "Z"].join(" ");
        } else if (i.type === "vertex") {
            let size = Math.max(i.width, i.height, i.radius, vxSize);
            d += ["M", i.x - size/2, i.y - size/2].join(" ");
            d += ["L", i.x + size/2, i.y - size/2].join(" ");
            d += ["L", i.x + size/2, i.y + size/2].join(" ");
            d += ["L", i.x - size/2, i.y + size/2, "Z"].join(" ");
        }
    }
    return d;
}

