const getReusableScales = (fieldType, channel, scene) => {
    let r = {};
    for (let itmKey in scene.encodings) {
        let encs = scene.encodings[itmKey];
        for (let c in encs) {
            let enc = encs[c];
            if (canShareScale(c, channel) && enc.dataTable.getFieldType(enc.field) === fieldType) {
                if (!(enc.scale.id in r))
                    r[enc.scale.id] = [];
                r[enc.scale.id].push(enc);
            }   
        }
    }
    return r;
}

const getAggregator = (channel, selected, directSelected, scene) => {
    let item;
    if (selected.length >= 1) {
        item = scene.getItem(selected[0]);
    } else if (directSelected) {
        item = scene.getItem(directSelected);
    }
    if (!item) return null;
    if (!item.dataScope) return null;
    if (item.dataScope.numTuples === 1) return null; 
    let enc = scene.getEncodingByItem(item, channel);
    if (!enc) return null;
    return enc.aggregator;
}

const getScaleType = (channel, selected, directSelected, scene) => {
    let item;
    if (selected.length >= 1) {
        item = scene.getItem(selected[0]);
    } else if (directSelected) {
        item = scene.getItem(directSelected);
    }
    if (!item) return null;
    let enc = scene.getEncodingByItem(item, channel);
    if (!enc) return null;
    return enc.scale.type;
}

const canShareScale = (channel1, channel2) => {
    const candidates = [["x", "width"], ["y", "height"], ["fillColor", "strokeColor"]];
    for (let c of candidates) {
        if (c.indexOf(channel1) >= 0 && c.indexOf(channel2) >= 0) {
            return true;
        }
    }
    return false;
}

const getDefaultCollectionLayout = (item, numItems) => {
    let ar = item.bounds.width/item.bounds.height,
        doubleGap = item.type === "collection" && item.layout && item.layout.type === "grid",
        rowGap = doubleGap ? item.layout.rowGap * 2 : 5,
        colGap = doubleGap ? item.layout.colGap * 2 : 5;
    // if (numItems > 50) {
    //     return atlas.layout("grid", {"numRows": Math.floor(Math.sqrt(numItems)), "rowGap": rowGap, "colGap": colGap});
    // } else {
    if (ar > 2) {
        return atlas.layout("grid", {"numCols": 1, "rowGap": rowGap});
    } else {
        return atlas.layout("grid", {"numRows": 1, "colGap": colGap});
    }
    //}
}

const getPositionInScene = (clientX, clientY) => {
    const rect = document.getElementById("canvas").getBoundingClientRect();
    return [clientX - rect.x, clientY - rect.y];
}

const getPositionInWindow = (sceneX, sceneY) => {
    const rect = document.getElementById("canvas").getBoundingClientRect();
    return [sceneX + rect.x, rect.y + sceneY];
}

const getDefaultLayout = (layout) => {
    switch (layout) {
        case "stack":
            return atlas.layout("stack", {orientation: "vertical"});
        case "packing":
            return atlas.layout("packing");
        case "none":
            return undefined;
        case "grid":
        default:
            return atlas.layout("grid", {numRows: 1});
    }
}

const getPropertyID = (itemType, prop) => {
    switch (itemType) {
        case "vertex":
            return "vx" + prop.replace(/^\w/, c => c.toUpperCase());;
        case "scene":
            return "canvas" + prop.replace(/^\w/, c => c.toUpperCase());;
        case "collection": 
        case "segment":
        case "legend":
            return itemType + prop.replace(/^\w/, c => c.toUpperCase());
        default:
            return prop;
    }
}

const getPropertyDef = (itemType, prop) => {
    const id = getPropertyID(itemType, prop);
    for (let g in properties) {
        for (let p in properties[g]) {
            if (properties[g][p].id === id)
                return properties[g][p];
        }
    }
    return null;
}

export {addToStack, getSVGPathData, getDefaultLayout, getScaleType, getAggregator, getReusableScales, getPositionInScene, getPositionInWindow, getPropertyID, getPropertyDef, getDefaultCollectionLayout};