import './css/ToolBar.css';
import { useDispatch } from "react-redux";
import { activateTool } from './toolSlice';
import { connect } from "react-redux";
import { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';
import { canDensify, canDivide, canRepeat } from 'atlas-vis';
import { Button, Modal } from "react-bootstrap";
import * as atlas from "atlas-vis";
import { addToStack, getDefaultCollectionLayout } from './Util';
import store from "./store";
import { select, joinDataItem, directSelect, updateUndoStack, updateRedoStack } from "./canvasSlice";
import {saveAs} from "file-saver";

const tools = ["Select", "DirectSelect", "Line", "Rect", "Ellipse", "Ring", "Pen", "Text"];

const mapStateToProps = state => ({
    activeTool: state.tool.activeTool,
    selected: state.canvas.selected,
    undoStackChanged: state.canvas.undoStackChanged,
    redoStackChanged: state.canvas.redoStackChanged
});

const renderer = atlas.renderer("svg", "preview")
    const scn = atlas.scene();

// const drawPreview = (selectedItem, cmd, dataset, field) => {
//     if (d3.select("#preview").empty())
//         return;
//     scn.removeAll();
//     if (selectedItem && cmd && field && dataset) {
//         let item = selectedItem.duplicate();
//         scn.addChild(item);
//         if (cmd === "Repeat") {
//             let coll = scn.repeat(item, dataset, {field: field, layout: getDefaultCollectionLayout(item.bounds, dataset.getUniqueFieldValues(field).length)});
//             let vb = [coll.bounds.left, coll.bounds.top, coll.bounds.width, coll.bounds.height].join(" ");
//             d3.select("#preview").attr("viewBox", vb);
//         }
