//     }
// }

const getUsableTbls = (item, datasets) => {
    let t = {};
    if (!item) return t;
    if (item.dataScope) {
        t[item.dataScope.dataTable.id] = item.dataScope.dataTable;
    } else {
        for (let d of datasets) {
            t[d.id] = d;
        }
    }
    return t;
}

const getOrientations = function(type, cmd) {
    if (cmd === "Divide") {
        switch (type) {
            case "rect":
                return ["horizontal", "vertical"];
            case "circle":
                return ["angular", "radial"];
            case "ring":
                return ["angular"];
            case "pie":
                return ["radial"];
            default:
                return ["horizontal", "vertical"];
        }
    } else if (cmd === "Densify") {
        return ["horizontal", "vertical"];
    }
}

function ToolBar(props) {

    const dispatch = useDispatch();
    const inputFile = useRef(null);

    const [showRepeatModal, setShowRepeatModal] = useState(false);
    //const [selectedItem, setSelectedItem] = useState(null);
    // const [datasetId, setDatasetId] = useState(null);
    //const [dataset, setDataset] = useState(undefined);
    const [cmd, setCmd] = useState(null);
    const [tbls, setTbls] = useState({});
    const [fields, setFields] = useState([]);
    const [orientations, setOrientations] = useState([]);
    const [joinMsg, setJoinMsg] = useState("");
    
    const doJoin = () => {
        setShowRepeatModal(false);
        addToStack(props.scene, props.datasets, props.undoStack);
        if (cmd === "Repeat") {
            let item = props.scene.getItem(props.selected[0]);
            store.dispatch(select([]));
            let dataset = tbls[d3.select("#datasetSelect").property("value")],
                field = d3.select("#fieldSelect").property("value");
            let layout = getDefaultCollectionLayout(item, dataset.getUniqueFieldValues(field).length);
            let coll = props.scene.repeat(item, dataset, {field: field, layout: layout});
            if (layout.numCols === 1) {
                props.scene.axis("y", field, {item: item});
            } else if (layout.numRows === 1) {
                props.scene.axis("x", field, {item: item});
            }
            props.scene.reCreateRelatedAxes(coll);
            store.dispatch(select([coll.id]));
            store.dispatch(joinDataItem({}));
        } else if (cmd === "Divide") {
            let item = props.scene.getItem(props.selected[0]);
            store.dispatch(select([]));
            let dataset = tbls[d3.select("#datasetSelect").property("value")],
                field = d3.select("#fieldSelect").property("value"), 
                o = d3.select("#orientationSelect").property("value");
            let coll = props.scene.divide(item, dataset, {field: field, orientation: o});
            store.dispatch(select([coll.id]));
            store.dispatch(joinDataItem({}));
        } else if (cmd === "Densify") {
            let item = props.scene.getItem(props.selected[0]);
            store.dispatch(select([]));
            let dataset = tbls[d3.select("#datasetSelect").property("value")],
                field = d3.select("#fieldSelect").property("value"), 
                o = d3.select("#orientationSelect").property("value");
            let itm = props.scene.densify(item, dataset, {field: field, orientation: o});
            store.dispatch(select([itm.id]));
            store.dispatch(joinDataItem({}));
        }
    }

    const switchTool = (t) => {
        d3.select("#textElement").style("visibility", "hidden");
        props.renderer.render(props.scene);
        switch (t) {
            case "Rect":
            case "Ellipse":
            case "Line":
            case "Ring":
            case "Pen":
                dispatch(directSelect(null));
                dispatch(select([]));
                d3.select("#evtLayer").style("cursor", "crosshair");
                break;
            case "Select":
                // dispatch(select([]));
                d3.select("#evtLayer").style("cursor", "default");
                dispatch(directSelect(null));
                break;
            case "DirectSelect":
                d3.select("#evtLayer").style("cursor", "default");
                dispatch(select([]));
                break;
            default:
                d3.select("#evtLayer").style("cursor", "default");
                break;
        }
        dispatch(activateTool(t));
    }

    const repeatBtnClicked = (e) => {
        switchTool("Select");
        if (props.selected.length === 0) return;
        setCmd("Repeat");
        setShowRepeatModal(true);
        let item = props.scene.getItem(props.selected[0]);
        let t = getUsableTbls(item, props.datasets);
        // let options = d3.select("#datasetSelect").selectAll("option").data(Object.values(t));
        // options.enter().append("option").merge(options).property("key", d => d.id).text(d => d.name);
        // options.exit().remove();
        setTbls(t);
        let usedFields = item.firstChild && item.firstChild.dataScope ? item.firstChild.dataScope.fields: [], 
            fields = t[Object.keys(t)[0]].nonNumericFields.concat(["atlas_rowId"]);
        fields = fields.filter(d => usedFields.indexOf(d) < 0);
        setFields(fields);
    }

    const divideBtnClicked = (e) => {
        switchTool("Select");
        if (props.selected.length === 0) return;
        setCmd("Divide");
        setShowRepeatModal(true);
        let item = props.scene.getItem(props.selected[0]);
        setOrientations(getOrientations(item.type, "Divide"));
        let t = getUsableTbls(item, props.datasets);
        setTbls(t);
        let usedFields = item.dataScope ? item.dataScope.fields: [],
            fields = t[Object.keys(t)[0]].nonNumericFields.concat(["atlas_rowId"]);
        fields = fields.filter(d => usedFields.indexOf(d) < 0);
        setFields(fields);
    }

    const densifyBtnClicked = (e) => {
        switchTool("Select");
        if (props.selected.length === 0) return;
        setCmd("Densify");
        setShowRepeatModal(true);
        let item = props.scene.getItem(props.selected[0]);
        setOrientations(getOrientations(item.type, "Densify"));
        let t = getUsableTbls(item, props.datasets);
        setTbls(t);
        let usedFields = item.dataScope ? item.dataScope.fields: [],
            fields = t[Object.keys(t)[0]].nonNumericFields.concat(["atlas_rowId"]);
        fields = fields.filter(d => usedFields.indexOf(d) < 0);
        setFields(fields);
    }

    const exportSVG = (e) => {
        let svg = document.getElementById("canvas");
        var serializer = new XMLSerializer();
        var source = serializer.serializeToString(svg);

        //add name spaces.
        if(!source.match(/^<svg[^>]+xmlns="http:\/\/www\.w3\.org\/2000\/svg"/)){
            source = source.replace(/^<svg/, '<svg xmlns="http://www.w3.org/2000/svg"');
        }
        if(!source.match(/^<svg[^>]+"http:\/\/www\.w3\.org\/1999\/xlink"/)){
            source = source.replace(/^<svg/, '<svg xmlns:xlink="http://www.w3.org/1999/xlink"');
        }

        //add xml declaration
        source = '<?xml version="1.0" standalone="no"?>\r\n' + source;
        let tbls = props.scene.getDataTables();
        let fname = (Object.values(tbls).length > 0) ? Object.values(tbls)[0].name + ".svg" : "visualization.svg";
        saveAs(new Blob([source], {"type":"text/plain;charset=utf-8"}), fname);
    }

    const saveScene = (e) => {
        let tbls = props.scene.getDataTables();
        let fname = (Object.values(tbls).length > 0) ? Object.values(tbls)[0].name + ".diproject" : "visualization.diproject";
        let scn = JSON.stringify(props.scene.toJSON());
        saveAs(new Blob([scn], {"type":"text/plain;charset=utf-8"}), fname);
    }

    const fileChanged = function(e) {
        e.stopPropagation();
        e.preventDefault();
        let f = e.target.files[0];
        const reader = new FileReader();
        reader.onload = function() {
            props.loadScene(JSON.parse(reader.result));
            props.resetStacks();
        }
        reader.readAsText(f);
    }

    const undo = (e) => {
        // console.log("undoing", props.undoStack.length);
        if (props.undoStack.length === 0) return;
        addToStack(props.scene, props.datasets, props.redoStack, "redo");

        let scn = props.undoStack.pop();
        props.loadScene(JSON.parse(scn));
        // console.log("finished undo", props.undoStack.length);
        store.dispatch(updateUndoStack({length: props.undoStack.length}));
    }

    const redo = (e) => {
        if (props.redoStack.length === 0) return;
        addToStack(props.scene, props.datasets, props.undoStack);

        let scn = props.redoStack.pop();
        props.loadScene(JSON.parse(scn));
        store.dispatch(updateRedoStack({length: props.redoStack.length}));
        //props.setRedoStack(props.redoStack.slice());
    }

    const openScene = (e) => {
        inputFile.current.click();
    }

    const updatePreview = () => {
        if (d3.select("#preview").empty())
            return;
        scn.clear();
        let selected = props.scene.getItem(props.selected[0]),
            dataset = tbls[d3.select("#datasetSelect").property("value")],
            field = d3.select("#fieldSelect").property("value");
        if (selected && cmd && field && dataset) {
            let item = selected.duplicate();
            if (item.fillColor === "none")
                item.fillColor = "#f5f5f5";
            item.strokeColor = "#222";
            scn.addChild(item);
            if (cmd === "Repeat") {
                let coll = scn.repeat(item, dataset, {field: field, layout: getDefaultCollectionLayout(item, dataset.getUniqueFieldValues(field).length)});
                let vb = [coll.bounds.left - 2, coll.bounds.top - 2, coll.bounds.width + 4, coll.bounds.height + 4].join(" ");
                d3.select("#preview").attr("viewBox", vb);
            } else if (cmd === "Divide") {
                item.strokeWidth = Math.max(item.strokeWidth, 1);
                let o = d3.select("#orientationSelect").property("value");
                let coll = scn.divide(item, dataset, {field: field, orientation: o});
                let vb = [coll.bounds.left - 2, coll.bounds.top - 2, coll.bounds.width + 4, coll.bounds.height + 4].join(" ");
                d3.select("#preview").attr("viewBox", vb);
            } else if (cmd === "Densify") {
                item.strokeWidth = Math.max(item.strokeWidth, 1);
                let o = d3.select("#orientationSelect").property("value");
                let itm = scn.densify(item, dataset, {field: field, orientation: o});
                scn.setProperties(itm, {"vxShape": "rect", "vxWidth": 4, "vxHeight": 4, "vxStrokeColor": "#1592e6", "vxStrokeWidth": 1});
                let vb = [itm.bounds.left - 2, itm.bounds.top - 2, itm.bounds.width + 4, itm.bounds.height + 4].join(" ");
                d3.select("#preview").attr("viewBox", vb);
            }
            renderer.clear();
            renderer.render(scn);
        }
    }

    const updateMsg = () => {
        if (props.selected.length === 0 || !cmd) {
            setJoinMsg("");
            return;
        }
        let selected = props.scene.getItem(props.selected[0]),
            dataset = tbls[d3.select("#datasetSelect").property("value")],
            field = d3.select("#fieldSelect").property("value");
        let vals = dataset.getUniqueFieldValues(field), num, msg = "";
        if (selected.dataScope) {
            num = selected.dataScope.getUniqueFieldValues(field).length;
        } else {
            num = vals.length;
        }
        if (cmd === "Repeat") {
            msg = [num, selected.type + "s", "will be created,"].join(" ");
        } else if (cmd === "Divide") {
            msg = ["Divide the", selected.type, "into", num, "parts,"].join(" ");
        } else if (cmd === "Densify") {
            if (selected.type === "rect") {
                let o = d3.select("#orientationSelect").property("value");
                msg = ["Densify the", o === "horizontal" ? "top/bottom side" : "left/right side", "of the rect with", num, "vertices,"].join(" ");
            } else {
                msg = ["Densify the", selected.type, "with", num, "vertices, one for each"].join(" ");
            }
        }
        d3.select("#msg").text(msg);
    }

    useEffect(() => {
        d3.selectAll(".iconBtn").classed("active", false);
        d3.select("#"+props.activeTool+"Btn").classed("active", true);
    }, [props.activeTool]);

    //manually trigger change event because repopulating options does not fire change event
    useEffect(() => {
        let fs = document.getElementById("fieldSelect");
        if (fs) {
            // let evt = document.createEvent("Event");
            // evt.initEvent('change', true, false);
            let evt = new CustomEvent('change', {bubbles: true, cancelable: false});
            fs.dispatchEvent(evt);
        }
    }, [fields])

    useEffect(() => {
        if (props.selected.length === 0) {
            d3.select("#repeatBtn").property("disabled", true);
            d3.select("#divideBtn").property("disabled", true);
            d3.select("#densifyBtn").property("disabled", true);
            return;    
        }
        let item = props.scene.getItem(props.selected[0]);
        d3.select("#repeatBtn").property("disabled", !item || !canRepeat(item) || props.datasets.length === 0);
        d3.select("#divideBtn").property("disabled", !item || !canDivide(item) || props.datasets.length === 0);
        d3.select("#densifyBtn").property("disabled", !item || !canDensify(item) || props.datasets.length === 0);
    }, [props.selected, props.scene, props.datasets]);

    useEffect(() => {
        d3.select("#undoBtn").property("disabled", props.undoStackChanged.length <= 0);
    }, [props.undoStackChanged]);

    useEffect(() => {
       d3.select("#redoBtn").property("disabled", props.redoStackChanged.length <= 0);
    }, [props.redoStackChanged]);

    useEffect(() => {
        d3.select("#undoBtn").property("disabled", props.undoStack.length <= 0);
        d3.select("#redoBtn").property("disabled", props.redoStack.length <= 0);
    }, [props.undoStack, props.redoStack])

    return (
        <div>
            <div className="myToolBar">
                <div className="myBtnGroup">
                    <button key="open" className="myTextBtn" id="openBtn" onClick={openScene}>Open</button>
                    <input type='file' id='scnFile' ref={inputFile} style={{display: 'none'}} accept=".alscn" onChange={fileChanged}/>
                    <button key="save" className="myTextBtn" id="saveBtn" onClick={saveScene}>Save</button>
                    <button key="export" className="myTextBtn" id="exportBtn" onClick={exportSVG}>Export</button>
                </div>
                <div className="myBtnGroup">
                    <button key="undo" className="myTextBtn" id="undoBtn" onClick={undo}>Undo</button>
                    <button key="redo" className="myTextBtn" id="redoBtn" onClick={redo}>Redo</button>
                </div>
                <div className="myBtnGroup">
                    {tools.map(t => <button key={t} className="iconBtn" title={t} id={t+"Btn"} tool={t} onClick={(e) => switchTool(e.target.getAttribute("tool"))}></button>)}
                </div>
                <div className="myBtnGroup">
                    <button key="repeat" className="myTextBtn" id="repeatBtn" onClick={repeatBtnClicked}>Repeat</button>
                    <button key="divide" className="myTextBtn" id="divideBtn" onClick={divideBtnClicked}>Divide</button>
                    <button key="densify" className="myTextBtn" id="densifyBtn" onClick={densifyBtnClicked}>Densify</button>
                </div>
            </div>
            <Modal show={showRepeatModal} onShow={(e)=>{updatePreview();updateMsg();}}  backdrop="static" keyboard={false}>
                <Modal.Header>
                <Modal.Title>{cmd}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                <div style={{"clear": "both", "marginBottom": "10px"}}>
                    <div id="msg" style={{"clear": "both", "marginBottom": "7px", "display": "block"}}>{joinMsg}</div>
                    one for each <select id="fieldSelect" onChange={(e)=>{updatePreview();updateMsg();}} style={{"marginLeft": "8px"}}>
                        {fields.map(o => <option value={o} key={o}>{o === "atlas_rowId" ? "row of data" : o}</option>)}
                    </select>
                    <div style={{"marginTop": "12px", "clear": "left"}}>from dataset 
                        <select id="datasetSelect" onChange={(e) => {setFields(tbls[e.target.value].nonNumericFields);}} style={{"marginLeft": "7px"}}>
                            {Object.values(tbls).map( o => <option value={o.id} key={o.id}>{o.name}</option>)}
                        </select>
                    </div>
                    <div style={{"marginTop": "15px", "display": cmd === "Repeat" || (cmd === "Densify" && props.selected[0] && props.scene.getItem(props.selected[0]).type !== "rect") ? "none" : "block"}}>
                        Orientation 
                        <select id="orientationSelect" onChange={(e)=>{updatePreview()}} style={{"marginLeft": "7px"}}>
                            {orientations.map(o => <option value={o} key={o}>{o}</option>)}
                        </select>
                    </div>
                </div>
                <svg id="preview" style={{"width": "470px", "height": "180px", "border": "solid 0px #ddd", "padding": "5px"}}></svg>
                </Modal.Body>
                <Modal.Footer>
                <Button variant="secondary" onClick={(e) => {setShowRepeatModal(false)}}>
                    Cancel
                </Button>
                <Button variant="primary" onClick={doJoin}>OK</Button>
                </Modal.Footer>
            </Modal>
        </div>
    )

}

export default connect(mapStateToProps)(ToolBar);
